@import url("https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,300&display=swap");

header{
    display: grid;
    justify-items: center;
}

.NavBar {
    display: flex;
    justify-content: space-around;
    font-size: 2vw;
    font-family: "Poppins";
    position: fixed;
    width: 96%;
    top:2px;
    z-index: 10;
    border-radius: 10px;
}

.NavBar-Dark{
    border: black solid thick;
    background: rgb(190,190,190);
    background: linear-gradient(0deg, rgba(190,190,190,1) 0%, rgba(130,130,130,1) 50%, rgba(190,190,190,1) 100%); 
}
.NavBar-Purple{
    border: rgb(0, 0, 50) solid thick;
    background: rgb(59,0,98);
    background: linear-gradient(0deg, rgba(59,0,98,1) 22%, rgba(65,0,0,1) 100%); 
    color: white;
}
.NavBar-Light{
    border: whitesmoke solid thick;
    background: white;
    border-radius: 0;
}

.NavLink, .off, a, span, .dropdown{
    flex-grow: 1;
    cursor: pointer;
    flex-shrink: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}

.NavBar-Dark, .NavBar-Light, .NavBar-Purple, .DropDown-Dark, .DropDown-Light, .DropDown-Purple{
    border-radius: 10px;
}

.dropdown, .NavBar-Dark a, .NavBar-Light a, .NavBar-Purple a{
    border-radius: 4px;
}


.dropdown button{
    font-size: 2vw;
    font-family: 'poppins';
    align-self: flex-end;
    color: inherit;
    background-color: inherit;
    border:inherit;
    width: 100%;
}

.NavLink:hover, .dropdown:hover {
    color: white;
    background: black;
}

.mobileOn{
    display: none;
}

.NavLink-Purple{
    color:white;
    background: rgb(59,0,98);
    background: linear-gradient(0deg, rgba(59,0,98,1) 22%, rgba(65,0,0,1) 100%); 
}
.NavLink-Dark{
    color:black;
    background: rgb(190,190,190);
    background: linear-gradient(0deg, rgba(190,190,190,1) 0%, rgba(130,130,130,1) 50%, rgba(190,190,190,1) 100%); 
}
.NavLink-Light{
    color: black;
    background-color: white;
}

.DropDown-Dark{
    background: rgb(190,190,190);
    background: linear-gradient(0deg, rgba(190,190,190,1) 0%, rgba(130,130,130,1) 50%, rgba(190,190,190,1) 100%); 
    border: black solid thick;
}
.DropDown-Light{
    background-color: white;
    border: whitesmoke solid thick;
}
.DropDown-Purple{
    background-color: rgb(50, 0, 50);
    border: black solid thick;
}

@media (pointer:none),(pointer:coarse) {
    .mobileOff{
        display: none;
    }
    .mobileOn{
        display: flex;
    }
    a, span{
        font-size: 1rem;
    }
    .dropdown{
        justify-content: end;
    }
    .dropdown button{
        align-self: flex-end;
        color: white;
        background-color: blue;
        border:blue solid thick;
        border-radius: 5px;
        width: 30vw;
    }
    .dropdown-menu{
        border-radius: 10px;
        width: 30vw;
    }
}
