.portfolioItems{
    width: 20vw;
    height: 20vw;
    color: white;
    flex-grow: 0;
    border-radius: 1vw;
    font-size: 2vw;
    text-align: center;
    box-shadow: 0vw 0vw 1vw 0.1vw rgb(0 0 0 / 50%);
    transition: all .2s ease-in-out;
}

.portfolioItems-Purple{
    box-shadow: 0vw 0vw 1vw 0.1vw rgb(255 255 255 / 50%);
}

.portfolioItems:hover{
    transform: scale(1.1);
}