@import url(https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

header{
    display: grid;
    justify-items: center;
}

.NavBar {
    display: flex;
    justify-content: space-around;
    font-size: 2vw;
    font-family: "Poppins";
    position: fixed;
    width: 96%;
    top:2px;
    z-index: 10;
    border-radius: 10px;
}

.NavBar-Dark{
    border: black solid thick;
    background: rgb(190,190,190);
    background: linear-gradient(0deg, rgba(190,190,190,1) 0%, rgba(130,130,130,1) 50%, rgba(190,190,190,1) 100%); 
}
.NavBar-Purple{
    border: rgb(0, 0, 50) solid thick;
    background: rgb(59,0,98);
    background: linear-gradient(0deg, rgba(59,0,98,1) 22%, rgba(65,0,0,1) 100%); 
    color: white;
}
.NavBar-Light{
    border: whitesmoke solid thick;
    background: white;
    border-radius: 0;
}

.NavLink, .off, a, span, .dropdown{
    flex-grow: 1;
    cursor: pointer;
    flex-shrink: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}

.NavBar-Dark, .NavBar-Light, .NavBar-Purple, .DropDown-Dark, .DropDown-Light, .DropDown-Purple{
    border-radius: 10px;
}

.dropdown, .NavBar-Dark a, .NavBar-Light a, .NavBar-Purple a{
    border-radius: 4px;
}


.dropdown button{
    font-size: 2vw;
    font-family: 'poppins';
    align-self: flex-end;
    color: inherit;
    background-color: inherit;
    border:inherit;
    width: 100%;
}

.NavLink:hover, .dropdown:hover {
    color: white;
    background: black;
}

.mobileOn{
    display: none;
}

.NavLink-Purple{
    color:white;
    background: rgb(59,0,98);
    background: linear-gradient(0deg, rgba(59,0,98,1) 22%, rgba(65,0,0,1) 100%); 
}
.NavLink-Dark{
    color:black;
    background: rgb(190,190,190);
    background: linear-gradient(0deg, rgba(190,190,190,1) 0%, rgba(130,130,130,1) 50%, rgba(190,190,190,1) 100%); 
}
.NavLink-Light{
    color: black;
    background-color: white;
}

.DropDown-Dark{
    background: rgb(190,190,190);
    background: linear-gradient(0deg, rgba(190,190,190,1) 0%, rgba(130,130,130,1) 50%, rgba(190,190,190,1) 100%); 
    border: black solid thick;
}
.DropDown-Light{
    background-color: white;
    border: whitesmoke solid thick;
}
.DropDown-Purple{
    background-color: rgb(50, 0, 50);
    border: black solid thick;
}

@media (pointer:none),(pointer:coarse) {
    .mobileOff{
        display: none;
    }
    .mobileOn{
        display: flex;
    }
    a, span{
        font-size: 1rem;
    }
    .dropdown{
        justify-content: end;
    }
    .dropdown button{
        align-self: flex-end;
        color: white;
        background-color: blue;
        border:blue solid thick;
        border-radius: 5px;
        width: 30vw;
    }
    .dropdown-menu{
        border-radius: 10px;
        width: 30vw;
    }
}

.Main-Dark{
    background: rgb(2,0,36);
    background: linear-gradient(322deg, rgba(2,0,36,1) 0%, rgba(9,121,104,1) 75%, rgba(0,212,255,1) 100%); 

}
.Main-Light{
    background-color: white;
}
.Main-Purple{
    background-color: black;

}

.Main{
    width: 100%;
    padding-top: 5vw;
}
.portfolioContainer{
        display: grid;
        flex-wrap: wrap;
        justify-content: center;
        grid-gap: 5vw;
        gap: 5vw;
        grid-template-columns: auto auto auto;
        text-shadow: 0 0 5px #000000, 0 0 5px #000000;
        padding-bottom: 5vw;
}
.portfolioItems{
    width: 20vw;
    height: 20vw;
    color: white;
    flex-grow: 0;
    border-radius: 1vw;
    font-size: 2vw;
    text-align: center;
    box-shadow: 0vw 0vw 1vw 0.1vw rgb(0 0 0 / 50%);
    transition: all .2s ease-in-out;
}

.portfolioItems-Purple{
    box-shadow: 0vw 0vw 1vw 0.1vw rgb(255 255 255 / 50%);
}

.portfolioItems:hover{
    transform: scale(1.1);
}
